$white:#ffffff;
$blue:#153136;
$brown:#B9966E;
$yellow:#C49E5F;

@font-face {
  font-family: poppinsL;
  src: url("./fonts/Poppins-Light.ttf");
}
@font-face {
  font-family: poppinsI;
  src: url("./fonts/Poppins-Italic.ttf");
}
@font-face {
  font-family: poppinsM;
  src: url("./fonts/Poppins-Medium.ttf");
}
@font-face {
  font-family: poppinsELI;
  src: url("./fonts/Poppins-ExtraLightItalic.ttf");
}
@font-face {
  font-family: poppinsLI;
  src: url("./fonts/Poppins-LightItalic.ttf");
}
*{
    font-family: poppinsM;
    &::selection { 
      background: $brown; 
      color: $blue;
    }
  }
.App {
 color:$white;
 min-height: 100vh;
 overflow: hidden;
 background-color: #0B1A1D;
//  cursor: url("images/blueEllipse.png") 4 12, auto !important; 

 .navbar{
   position: fixed;
   width: 100%;
   padding: 20px 40px;
   z-index: 25;
  //  background: linear-gradient(to top,transparent 5%,#5D4B37 5% , $brown);
   .navbar-nav{
     width: 100%;
     display: flex;
     justify-content: flex-end;
      .nav-link{
        margin: 0 4%;
        color: $white;
        position: relative;
        height: fit-content;
        &::before{
          transition: 1s;
          background-color: $white;
          content: '';
            opacity: 0;
            width: 0px;
            height: 2px;
            position: absolute;
            bottom: 8px;
            left: 8px;
        }
        &:hover{
          &.active{
            &::before{
              width: 80%;
            }
          }
        }
        &.active{
          &::before{
            width: 20px;
            opacity: 1;
            
            
          }
          
        }
      }
      a:hover{
        text-decoration: none;
      }
   }
 } 
 .home{
   background: linear-gradient(to bottom, #153136 0%, #0F2428 50%,#0E2024 75%,#0B1A1D 100%);
   padding: 15% 0 50%;
   height: 100vh;
  //  overflow: hidden;
   .title{
    font-size: 50px;
    font-weight: 700;
    line-height: 53px;
    margin-bottom: 20px;
    @media (max-width: 768px) {
      padding-top: 15%;
      font-size: 35px;
    }
   }
   .paragraph{
    font-size: 20px;
    @media (max-width: 768px) {
    font-size: 16px;
    }

   }
   .link{
     position: relative;
      display: block;
      margin-top: 30px;
      font-size: 20px;
      color:$white;
      &::before{
        content: '';
        background-color: $white;
        width: 20px;
        height: 2px;
        position: absolute;
        bottom: 0px;
        // left: 15px;
      }
      &:hover{
        text-decoration: none;
      }
   }
   .brown{
     color: $brown;
   }
   .home-pic-con{
     position: relative;
    @media (max-width: 768px) {
      padding: 10%;
    }
     .brown-circle{
       position: absolute;
       width: 800px;
       height: 800px;
      background: linear-gradient(330deg, #5D4B37, $brown);
       border-radius: 50%;
       bottom: 0;
       right: 0;
       transform: translate(40%,55%);
       z-index: 3;

     }
     .home-pic{
       width: 70%;
       height: auto;
       display: flex;
       margin: 0 auto;
       z-index: 4;
       position: relative;
       transform: rotateY(180deg) translate(-30%,-10%) ;
        @media (max-width: 768px) {
          height: 100%;
          width: 80%;
        }
     }
   }
 }
 .projects{
  z-index: 10;
  position: relative;
  // background: linear-gradient(to top ,#5D4B37, $brown);
  // background-color: transparent;
  padding: 10% 0 ;
  .projects-pic{
    width: 100%;
    height: auto;
    display: flex;
    margin: 0 auto;
    @media (max-width: 768px) {
      display: none;
    }
  }
  .circleTitle{
    position: relative;
    
    .text{
      position: relative;
      z-index: 2;
    }
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      transform: translate(-50%,-50%);
      background-color: $blue;
      width: 100px;
      height: 100px;
      border-radius: 50%;
    }
    &.lg{
      font-size: 40px;
      font-weight:700;
      transform: translateY(100px);
      opacity: 0;
      @media (max-width: 768px) {
        font-size: 26px;
        margin-left: 10%;
      }
      &::before{
        width: 160px;
        height: 160px;
        @media (max-width: 768px) {
          width:  110px;
          height: 110px;
          transform: translate(-30%,-50%);

        }
      }
    }
    &.md{
      font-size: 20px;
      font-family: poppinsL;
      margin-top: 60px;
      transform: translateY(100px);
      opacity: 0;
      @media (max-width: 768px) {
        margin-left: 10%;
        margin-top: 50px;
        margin-top: 50px;

      }
      &::before{
        width: 40px;
        height: 40px;
      }
    }
    &.sm{
      font-size: 20px;
      font-family: poppinsL;
      &::before{
        width: 16px;
        height: 16px;
        top:32%;
      }
    }
  }
  .ProjectsButtons{
    transform: translateY(100px);
    opacity: 0;
    margin: 25px 0 10px;
    button{
      border-color: $blue;
      background-color: $blue;
      margin: 0 10px 5px 0;
      padding: 5px 20px;
      opacity: .5;
      transform: .3s;
      &:active{
        opacity: 1;
      }
      &:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
        color: #fff;
        background-color: #264247;
        border-color: #264247;
      }
      &:not(:disabled):not(.disabled).active:focus, &:not(:disabled):not(.disabled):active:focus, .show>&.dropdown-toggle:focus {
        box-shadow: 0 0 0 0.2rem #15313655;
        opacity: 1;

      }
      &:focus{
        box-shadow: 0 0 0 0.2rem #15313655;
        opacity: 1;
      }
    }
  }
  .projectCard{
    transform: translateY(100px);
    opacity: 0;
    position: relative;
    width: 100%;
    display: block;
    // min-height: 200px;
    background-color: #ffffff44;
    border-radius: 10px;
    margin-bottom: 20px;
    padding: 10px 20px 60px;
    transition: 0.6s;
    color: $white;
    // @for $i from 1 through 20 {
    //   &:nth-child(#{$i}) {
    //     transition-delay:(#{$i*0.3s}); 
    //   }
    // }
    &:hover{
      text-decoration: none;
    }
    .circleTitle::before{
      transition: 0.6s;
    }
    .des{
      margin-top: 10px;
    }
    .date{
      position: absolute;
      bottom: 20px;
      right: 30px;
      color: #ffffffaa;
      font-family: poppinsI;
    }
    .link{
      position: relative;
      display: block;
      margin-top: 10px;
      font-size: 20px;
      color:$white;
      &::before{
        content: '';
        background-color: $white;
        width: 20px;
        height: 2px;
        position: absolute;
        bottom: 0px;
        // left: 15px;
      }
      &:hover{
        text-decoration: none;
      }
    }
    &.withLink{
      padding: 10px 20px 20px;
    }
    &:hover{
      background-color: $blue;
      .circleTitle::before{
        background: linear-gradient(-40deg, #5D4B37, $brown);
      }
    }
  }
 }
 .specificProject{
    overflow: hidden;
    background: linear-gradient(to bottom, #5D4B37, $brown);
    padding-top: 5%;
    position: relative;
    @media (max-width: 768px) {
      padding-top: 25%;
    }
    .parallaxCircle{
      position: absolute;
      width: 100px;
      height: 100px;
      background: linear-gradient(to top,#0A181A,#408088);
      border-radius: 50%;
      z-index: 19;
      right: 60px;
      top:10%;
      @media (max-width: 768px) {
        width: 50px;
        height: 50px;
      }
      &.rightTop{
        right: 60px;
        top:10%;
      }
      &.leftTop{
        left: 60px;
        top:5%;
      }
      &.rightMiddle{
        right: 60px;
        top:37%;
      }
      &.leftMiddle{
        left: 60px;
        top:42%;
      }
      &.rightBottom{
        right: 60px;
        top:65%;
      }
      &.leftBottom{
        left: 60px;
        top:70%;
      }
    }
   .bigTitle{
     position: relative;
     margin-bottom: 20vh;
     @media (max-width: 768px) {
      margin-bottom: 10vh;
     }
     .bigCircle{
       position: absolute;
       width: 250vh;
       height: 250vh;
       border-radius: 50%;
       background: linear-gradient(to bottom, #0a181a, #408088);
       top: 70%;
       left: 50%;
       transform: translate(-50%,-50%);
       @media (max-width: 768px) {
        width: 180vh;
        height: 180vh;
        }
     }
     .bgText{
      font-size: 190px;
      font-weight: 800;
      opacity: .1;
      user-select: none;
      z-index: 19;
      position: relative;
      @media (max-width: 768px) {
        font-size: 100px;
        text-align: center;
        line-height: 100px;
      }

     }
     .text{
       position: absolute;
       top: 50%;
       transform: translateY(-50%);
       left: 50px;
      @media (max-width: 768px) {
       left: 5%;

      }
       .title{
        font-weight: 800;
       }
       .paragraph{
        //  width: calc(100%-500px);
       }
     }
   }
   .paragraphCon{
     position: relative;
     margin: 60px 0;
     z-index: 20;
     .circle{
       width: 25px;
       height: 25px;
       border-radius: 50%;
       background: linear-gradient(120deg, #0a181a, #408088);
       position: absolute;
       left: 0;
       top: 5px;
     }
     .text{
       transform: translateX(40px);
        width: calc(100% - 40px);
       .title{
        @media (max-width: 768px) {
         margin-bottom: 10px;
         font-size: 18px;
        }
       }
       .paragraph{
        
       }
    }
   }
   .img{
     width: 80%;
     margin: 0 auto;
     display: flex;
     height: auto;
     z-index: 3;
     position: relative;
     mix-blend-mode: screen;
     &.luminosity{
       mix-blend-mode: luminosity;
     }
   }
   .contactMe{
     position: relative;
     font-size: 150px;
     text-align: center;
     margin: 30vh 0;
     @media (max-width: 768px) {
      font-size: 50px;
      margin: 10vh 0;
     }
     .text{
       position: relative;
     }
     .circle{
       position: absolute;
        width: 300px;
        height: 300px;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        border-radius: 50%;
       background: linear-gradient(300deg, #0a181a, #408088);
       @media (max-width: 768px) {
        width: 150px;
        height: 150px;
      }
     }
   }
   .media{
     display: flex;
     justify-content: center;
     .circle{
       width: 30px;
       height: 30px;
       background-color: $white;
       border-radius: 50%;
       margin: 0 5px;
       display: flex;
       justify-content: center;
       align-items: center;
       svg{
         color: $brown;
        //  width: 20px;
        //  height: 20px;
       }
     }
    }
    .designed{
      text-align: center;
      margin-top: 5px;
      margin-bottom: 15px;
      font-size: 15px;
    }
 }
 .partners{
   position: relative;
   z-index: 20;
    background: linear-gradient(to top, $blue 75%, transparent );
    min-height: 100vh;
    padding-top: 5%;
    .paragraphCircles{
      position: relative;
      margin-top: 10vh;

      p{
        font-size: 28px;
        padding: 40px 0 60px 80px;
        position: relative;
        z-index: 2;
        @media (max-width: 768px) {
          padding: 40px 0 60px 40px;
          font-size: 20px;
        }
      }
      .bigCircle{
        position: absolute;
        width: 150px;
        height: 150px;
        background: linear-gradient(330deg, #5D4B37, $brown);
        border-radius: 50%;
        top: 0;
        left: 0;
      }
      .smallCircle{
        position: absolute;
        width: 30px;
        height: 30px;
        background: linear-gradient(270deg, #5D4B37, $brown);
        border-radius: 50%;
        top:30px;
        left: -10px;
      }
    }
    .slider{
      position: relative;
        @media (max-width: 768px) {
          padding-bottom: 60px;
        }
      .arrows{
        position: absolute;
        right: 0;
        top:0;
        display: flex;
        justify-content: center;
        align-items: center;
        @media (max-width: 768px) {
          bottom:0;
          top: auto;

        
        }
        .sliderPages{
          padding: 0 10px;
          min-width: 80px;
          text-align: center;
        }
        .arrowBtn{
          border: none;
          background: linear-gradient(330deg, #5D4B37, $brown);
          width: 40px;
          height: 40px;
          border-radius: 50%;
          svg{
            color: $white;
          }
        }
      }
      .partnerIcon{
        .circleImgCon{
          position: relative;
          width: 65px;
          height: 65px;
          display: inline-block;
          vertical-align: middle;
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 3;
            position: relative;
            border:3.5px solid $white;
            border-radius: 50%;
          }
        }
        .partnerInfo{
          display: inline-block;
          vertical-align: middle;
          margin-left: 10px;
          .partnerName{
          }
          .partnerJob{
  
          }
        }
      }
      .paragraph{
        padding-left: 70px;
        padding-top: 20px;
        line-height: 28px;
        @media (max-width: 768px) {
          padding-left: 0px;
          min-height: 350px;
        }
      }
    }

 }
 .contact{
  position: relative;
  z-index: 21;
  padding-top: 10%;
  background: linear-gradient(to bottom, $blue 0%, #0F2428 50%,#0E2024 75%,#0B1A1D 100%);
  // height: 100vh;  
  overflow: hidden;
  position: relative;
  @media (max-width: 768px) {
    padding-top: 20%;
  }
  .title{
    position: relative;
    font-size: 50px;
    font-weight: bold;
    margin: 20px 0 30px;
    @media (max-width: 768px) {
      margin-left: 10%;
    }
    span{
      z-index: 10;
      position: relative;
    }
    .brownCircle{
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(-50%,-50%);
      width: 100px;
      height: 100px;
      background: linear-gradient(330deg, #5D4B37, $brown);
      border-radius: 50%;
      @media (max-width: 768px) {
        transform: translate(-30%,-50%);
      }
    }
  }
  .paragraph{
    // padding-left: 50px;
    font-size: 20px;
    width: 70%;
    font-family: poppinsL;

    @media (max-width: 768px) {
      width: 100%;
    }
  }
  form{
    input,textarea{
      width: 80%;
      background: rgba($color: $brown, $alpha: .5);
      border: none;
      border-radius: 7px;
      font-family: poppinsL;
      font-size: 16px;
      color: $white;
      margin: 10px 0;
      padding: 8px 20px;
      resize: none;
      &::placeholder{
        color: $white;
      }
      &:focus{
        outline: none;
      }
      @media (max-width: 768px) {
       width: 100%;
      }
    }
    button{
      display: flex;
      margin-left: auto;
      margin-right: 20%;
      background: linear-gradient(to left,$brown,#5D4B37);
      border: none;
      font-family: poppinsL;
      font-size: 16px;
      padding: 5px 40px;
      @media (max-width: 768px) {
        margin-right: 0%;
      }
      &:active{
        opacity: 1;
      }
      &:not(:disabled):not(.disabled):active, .show>.btn-primary.dropdown-toggle {
        color: #fff;
        background-color: #264247;
        border-color: #264247;
      }
      &:not(:disabled):not(.disabled).active:focus, &:not(:disabled):not(.disabled):active:focus, .show>&.dropdown-toggle:focus {
        box-shadow: 0 0 0 0.2rem rgba($color: $brown, $alpha: .2);
        opacity: 1;

      }
      &:focus{
        box-shadow: 0 0 0 0.2rem #15313655;
        opacity: 1;
      }
    }
  }
  .contact-pic-con{
      position: relative;
      .brown-circle{
        position: absolute;
        width: 800px;
        height: 800px;
        background: linear-gradient(to left,$brown,#5D4B37);
        border-radius: 50%;
        bottom: 0;
        right: 0;
        transform: translate(30%,60%);
        z-index: 3;
 
      }
      .contact-pic{
        width: 100%;
        height: auto;
        display: flex;
        // align-items: flex-end;
        margin-top: 15%;
        z-index: 4;
        position: relative;
        transform: rotateY(180deg);
      }
  }
  .media{
    display: flex;
    justify-content: center;
    position: absolute;
    right: 20px;
    bottom: 10px;
    z-index: 20;
    .circle{
      width: 30px;
      height: 30px;
      background-color: $white;
      border-radius: 50%;
      margin: 0 5px;
      display: flex;
      justify-content: center;
      align-items: center;
      svg{
        color: $brown;
       //  width: 20px;
       //  height: 20px;
      }
    }
  }
 }
 

}